import { Injectable } from "@angular/core";
import { of } from "rxjs";
import { take } from "rxjs/operators";

@Injectable({ providedIn: 'root' })
export class CharacterService {
    characterDataMock = {
        id: 12345,
        firstName: "John",
        lastName: "Smith",
        stateRole: "Judge"
    }

    getCharacterData() {
        return new Promise((resolve, reject) => {
            resolve(this.characterDataMock);
        });
    }
}